@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
:root {
  --primary: #284b63;
  --secondary: #353535;
  --primary-alt: #3c6e71;
  --background: #d9d9d9;
}

body {
  font-family: "Titillium Web", sans-serif;
  color: #353535;
  color: var(--secondary);
  background-color: #d9d9d9;
  background-color: var(--background);
}
.App {
  max-width: 960px;
  margin: 0 auto;
}
.title h1 {
  color: #284b63;
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2,
.title p {
  text-align: center;
}
.title h2 {
  margin-top: 60px;
  font-size: 2.6rem;
}

.hammer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.hammer > * {
  height: 3rem;
  width: 3rem;
}

input {
  display: none;
}

label {
  display: block;
  border: 1px solid var(--primary-alt);
  color: var(--primary-alt);
  padding: 1rem;
  border-radius: 50%;
  line-height: 1rem;
  font-size: 1.3rem;
  font-weight: bolder;
  transition: background-color 0.5s;
  transition-timing-function: ease;
}

label:hover {
  background-color: var(--primary);
  cursor: pointer;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.progress__bar {
  height: 5px;
  background: var(--primary);
  margin-top: 20px;
}

.img__grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  gap: 40px;
}

.img__wrap {
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  position: relative;
  opacity: 0.8;
}

.img__wrap img {
  min-width: 100%;
  min-height: 100%;
  max-height: 100%;
  max-width: 100%;

  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
  transition: -webkit-filter ease-in-out 0.3s;
  transition: filter ease-in-out 0.3s;
  transition: filter ease-in-out 0.3s, -webkit-filter ease-in-out 0.3s;
}

@media only screen and (max-width: 600px) {
  .img__grid {
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
  }
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.backdrop:hover {
  cursor: pointer;
}

.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
  cursor: default;
}

